.main-nav{
    width: 100%;
    min-height: 100vh;
    background-image: url(./assets/pet.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-attachment: fixed;
}

.main-nav:before{
    content: '';
    width: 100%;
    min-height: 100vh;
    position: absolute;
    top:0;
    left: 0;
    background-image: linear-gradient(rgba(255,255,255, .1), rgba(34,40,49, 1));
}
.text {
    min-height: 100vh;
    color: #f4f4f4;
    font-family: 'Outfit', sans-serif;
}

.text h2{
    font-family: 'Anton', sans-serif;
    font-size: 30px;
    line-height: 25px;
    margin-bottom: 30px;
    font-weight: 800;
}

.text span{
    font-family: 'Anton', sans-serif;
    letter-spacing: 3px;
    font-size: 1.3em;
    margin: 0 0 0 10px;
}

.text p{
    font-size: 1em;
}

.btn-yo{
    padding: 7px 12px;
    color: #f4f4f4;
    background-color: #c10000e0;
}

.btn-yo:hover{
    color: #f4f4f4;
    background-color: #ff0000;
}

.col-sm-8{
    position: relative;
}   
