/*Footer*/
.contaienr{
    position: absolute;
    width: 100%;
}

.footer{
    padding: 20px;
    text-align: center;
    background: rgb(48, 48, 48);
    color: #f4f4f4;
  }

.redes{
    margin-top: 40px;
    height: 100px;
    background-color: rgba(77, 73, 75, 0.4);
    display: flex;
    justify-content: space-around;
  }

.redes ul{
    margin: 0;
    padding: 0;
  }

.redes li{
    list-style-type: none;
    list-style-position: inside;
    display: block;
    padding: 30px 10px;
    float: left;
  }

.redes a{
    width: 100%;
    height: auto;
  }

.redes img{  
    width: 46px;
    height: auto;
  }

.redes img:hover{
    border: 0;
    filter: sepia(80%);
  }