@import url(https://fonts.googleapis.com/css2?family=Anton&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Anton&family=Montserrat:wght@100;300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Anton&family=Montserrat:wght@100;300;500&family=Outfit:wght@500&display=swap);
/*navbar*/
.bg-dark{
    position: relative;
    z-index: 20;
    width: 100%;
}

.container-nav{
    background: rgba(66, 65, 65, 0.7);
}

.nav a{
    color: #f4f4f4!important;
    font-family: 'Outfit', sans-serif;
    font-size: 23px;
    padding: 10px 15px;
    text-align: center;
}

.nav a:hover{
    background-color: #c10000a2;
    border-radius: 10px;
}





/*MyPets*/
#main-pets .pets{
    padding: 70px 0 50px;
}

#main-pets .list-pets{
    padding-top: 50px;
}

.list-pets .pets-block{
    margin-bottom: 25px;
}

.list-pets .pets-block .text-block{
    margin-left: 58px;
}

.list-pets .pets-block .text-block .pets-name{
    font-family: 'Anton', sans-serif;
    font-size: 20px;
    font-weight: 900;
    margin-bottom: 5px;
}

.list-pets .pets-block .text-block .pets-info{
    font-family: 'Outfit', sans-serif;
    font-size: 16px;
    font-weight: 300;
    margin-bottom: 10px;
}

.list-pets .pets-block .text-block .pets-text{
    font-family: 'Outfit', sans-serif;
    font-size: 12px;
    line-height: normal;
    font-weight: 300;
}

h2{
    font-family: 'Anton', sans-serif;
}

p{
    font-family: 'Outfit', sans-serif;
}

.text-color{
    color: #2ac5ed;
    font-weight:bold;
}  
.main-nav{
    width: 100%;
    min-height: 100vh;
    background-image: url(/static/media/pet.2cb51f82.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-attachment: fixed;
}

.main-nav:before{
    content: '';
    width: 100%;
    min-height: 100vh;
    position: absolute;
    top:0;
    left: 0;
    background-image: linear-gradient(rgba(255,255,255, .1), rgba(34,40,49, 1));
}
.text {
    min-height: 100vh;
    color: #f4f4f4;
    font-family: 'Outfit', sans-serif;
}

.text h2{
    font-family: 'Anton', sans-serif;
    font-size: 30px;
    line-height: 25px;
    margin-bottom: 30px;
    font-weight: 800;
}

.text span{
    font-family: 'Anton', sans-serif;
    letter-spacing: 3px;
    font-size: 1.3em;
    margin: 0 0 0 10px;
}

.text p{
    font-size: 1em;
}

.btn-yo{
    padding: 7px 12px;
    color: #f4f4f4;
    background-color: #c10000e0;
}

.btn-yo:hover{
    color: #f4f4f4;
    background-color: #ff0000;
}

.col-sm-8{
    position: relative;
}   

/*Footer*/
.contaienr{
    position: absolute;
    width: 100%;
}

.footer{
    padding: 20px;
    text-align: center;
    background: rgb(48, 48, 48);
    color: #f4f4f4;
  }

.redes{
    margin-top: 40px;
    height: 100px;
    background-color: rgba(77, 73, 75, 0.4);
    display: flex;
    justify-content: space-around;
  }

.redes ul{
    margin: 0;
    padding: 0;
  }

.redes li{
    list-style-type: none;
    list-style-position: inside;
    display: block;
    padding: 30px 10px;
    float: left;
  }

.redes a{
    width: 100%;
    height: auto;
  }

.redes img{  
    width: 46px;
    height: auto;
  }

.redes img:hover{
    border: 0;
    filter: sepia(80%);
  }
